.toggle {
    width: 56px;
    height: 36px;
    border: 2px solid black;
    border-bottom-width: 4px;
    border-radius: 10px;
    padding: 4px;
}

.toggle.closed  {
    background-color: gray;
}

.toggle.open {
    background-color: var(--dsek);
    padding-left: 26px;
}

.toggle:disabled {
    opacity: 0.5;
}

.dot {
    width: 22px;
    height: 22px;
    background-color: white;
    border-radius: 5px;
}