.container {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 50px;
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.option {
    width: 40px;
    height: 40px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.option:focus-visible {
    outline: 2px solid var(--highlight);
}

.option svg {
    color: gray;
}

.current {
    background-color: var(--dsek);
}

.current svg {
    color: black;
}