.Field {
    position: relative;
    width: 100%;
    height: 50px;
    border-radius: 10px;
}

.Field svg:not(.check) {
    position: absolute;
    width: 50px;
    height: 50px;
    background-color: black;
    border: 2px solid black;
    border-bottom-width: 4px;
    border-radius: 10px 0 0 10px;
    padding: 10px;
    color: white;
    transition-duration: 0.1s;
}

.Field input {
    position: absolute;
    width: 100%;
    height: 100%;
    border: 2px solid black;
    border-bottom-width: 4px;
    border-radius: 10px;
    padding-left: 60px;
    font-size: 100%;
}

.Field input:focus-visible {
    /* webkit bug */
    outline: none;
    appearance: initial;
    -webkit-appearance: none;
    -webkit-box-shadow: 0px 0px 0px 2px var(--highlight);
    box-shadow: 0px 0px 0px 2px var(--highlight);
}

.Field .check {
    position: absolute;
    right: 0;
    width: 50px;
    height: 50px;
    padding: 10px;
    color: limegreen;
    pointer-events: none;
}

.Field .left {
    position: absolute;
    right: 10px;
    height: calc(100% - 2px);
    display: flex;
    align-items: center;
    color: gray;
    pointer-events: none;
}