.suggestion {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
    gap: 10px;
}

.text {
    font-size: 150%;
    text-align: center;
}

.name {
    font-weight: bold;
}

.buttons {
    display: flex;
    gap: 10px;
}

.button {
    height: 50px;
    width: 50px;
    border: 2px solid black;
    border-bottom-width: 4px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.button:disabled {
    opacity: 0.5;
}