.Auth {
    width: 100%;
    min-height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px 0 20px 0;
}

.form {
    width: 100%;
    max-width: 250px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    flex-flow: column;
    gap: 20px;
    /* animation: opacity 500ms 1 forwards; */
}

@keyframes opacity {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
  }

.h1 {
    all: unset;
    font-size: 200%;
    font-weight: bold;
    text-align: center;
}

.error {
    background-color: transparent;
    color: red;
    font-weight: bold;
}

.link {
    font-weight: bold;
    text-decoration: none;
    color: black;
}

.link:focus-visible {
    outline: 0;
    text-decoration: underline;
}