.open {
    width: 50px;
    height: 50px;
    opacity: 0.5;
    border: 2px solid black;
    border-bottom-width: 4px;
    border-radius: 10px;
    padding: 10px;
}

.cover {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 50px;
    background-color: rgba(0, 0, 0, 0.75);
    display: flex;
    justify-content: center;
    z-index: 1
}

.content {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column;
    gap: 10px;
    padding: 10px;
    overflow-y: scroll;
}

.content::-webkit-scrollbar {
    display: none;
}

.content {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.input {
    height: 50px;
    min-height: 50px;
    width: 100%;
    background-color: lightgray;
    border: none;
    border: 2px solid black;
    border-radius: 10px;
    text-align: center;
    font-size: 100%;
}

.input:focus {
    outline: none;
}

.input:focus-visible {
    /* webkit bug */
    outline: none;
    appearance: initial;
    -webkit-appearance: none;
    -webkit-box-shadow: 0px 0px 0px 2px var(--highlight);
    box-shadow: 0px 0px 0px 2px var(--highlight);
}

.close {
    position: absolute;
    right: 12px;
    top: 12px;
    width: 46px;
    height: 46px;
    border: none;
    border-radius: 8px;
    background-color: transparent;
    display: flex;
    padding: 10px;
}

.x {
    width: 100%;
    height: 100%;
    opacity: 0.5;
}

.result {
    width: 100%;
    min-height: 50px;
    background-color: lightgray;
    border: 2px solid black;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    text-align: left;
    font-weight: bold;
}

.category {
    text-align: right;
    font-style: italic;
    font-weight: normal;
    color: gray;
}