.container {
    height: calc(100% - 50px);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
    padding: 10px;
}

.setting {
    width: 100%;
    max-width: 200px;
    height: 120px;
    border-color: lightgray;
    border-style: solid;
    border-width: 1px 0 1px 0;
    display: flex;
    align-items: center;
}

.setting:first-child {
    border-top-width: 0;
}

.setting:last-child {
    border-bottom-width: 0;
}

.setting > svg {
    position: absolute;
    width: 50px;
}

.info {
    text-decoration: none;
    font-weight: bold;
    color: black;
}

.content {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
    gap: 10px;
    text-align: center;
}

.logout {
    width: 100%;
    max-width: 150px;
    height: 50px;
    background-color: var(--dsek);
    border: 2px solid black;
    border-bottom-width: 4px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    font-weight: bold;
    padding: 10px;
}