.color {
    width: 30px;
    height: 30px;
    background-color: var(--color);
    border: 2px solid white;
    border-radius: 10px;
}

.hide {
    opacity: 0;
}

.range::-webkit-slider-thumb,
.range::-moz-range-thumb {
    background: transparent; 
    border-color: transparent;
    color: transparent;
}

.container {
    position: absolute;
    width: calc(100% - var(--padding) * 2);
    height: 30px;
}

.container > div {
    width: 100%;
    height: 100%;
    border: var(--border) solid white;
    border-radius: 10px;
}