.container {
    z-index: 10;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    height: 50px;
    border: 2px solid black;
    border-bottom-width: 4px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    animation: move calc(var(--duration) * 1ms) 1 forwards;
}

@keyframes move {
    0% {
        top: -60px;
    }
    20% {
        top: 10px;
    }
    80% {
        top: 10px;
    }
    100% {
        top: -60px;
    }
}

.on {
    background-color: limegreen;
}

.off {
    background-color: var(--dsek);
}

.container > svg {
    width: 50px;
}