.container {
    height: 100%;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
}

.h1 {
    z-index: 1;
    margin: 0;
    font-size: 3em;
}

.ul {
    z-index: 1;
    margin: 30px 0 30px 1em;
    padding: 0;
    list-style-type: '✔';
    font-weight: bold;
}

.ul li {
    margin-top: 5px;
    padding-left: 10px;
}

.ul li:first-child {
    margin-top: 0;
}

.icon {
    margin-bottom: 20px;
    width: 100px;
    height: 100px;
}

@media (max-height: 500px) {
    .icon {
        display: none;
    }
}

.Google,
.IOS,
.Web {
    width: 300px;
    height: 80px;
    background-color: black;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 20px;
    font-weight: bold;
    text-decoration: none;
    color: white;
    text-align: center;
}

.Google img,
.IOS img,
.Web svg {
    height: 40px;
    width: 40px;
}

.IOS img {
    margin-top: -5px;
}

.Google:focus-visible,
.IOS:focus-visible,
.Web:focus-visible {
    outline: 2px solid var(--highlight);
}

.other {
    margin-top: 10px;
    text-decoration: none;
    text-align: center;
    color: gray;
}

.other:focus-visible {
    outline: none;
    text-decoration: underline;
}