.document {
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
    position: absolute;
    top: 0;
}

.loading {
    opacity: 0;
}

.done {
    opacity: 1;
}

.horizontally {
    left: 50%;
    transform: translateX(-50%);
}

.vertically {
    top: 50%;
    transform: translateY(calc(-50% - 25px));
}

.hide {
    display: none;
}

.loader {
    width: 50px;
    height: 50px;
    position: absolute;
    left: 50%;
    top: 50%;
    --center: translate(-50%, calc(-50% - 50px));
    transform: var(--center);
    opacity: 0.5;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    from {
        transform: var(--center) rotate(0deg);
    }
    to {
        transform: var(--center) rotate(360deg);
    }
}