* {
  box-sizing: border-box;
  font-family: Arial;
  user-select: none;

  --dsek: #f280a1;
  --info: #9d64cd;
  --highlight: #3584e4;
}

html,
body,
#root {
  height: 100%;
  overflow: hidden;
}

body,
#root {
  min-height: 100%;
}

body {
  margin: 0;
  overscroll-behavior: contain;
}

p {
  margin: 0;
}

@keyframes opacity {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: transparent;
}