.container {
    height: 100%;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
}

.h2 {
    margin: 0;
    border-bottom: 2px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 10px 30px 10px 30px;
}

.ol {
    margin: 0;
    padding: 20px 20px 20px calc(20px + 1.1em);
}

.li {
    margin-top: 20px;
    padding-left: 10px;
}

.li:first-child {
    margin-top: 0;
}

.li::marker {
    font-weight: bold;
}

.p {
    height: 30px;
    display: flex;
    align-items: center;
}

.p span {
    font-weight: bold;
}

.share {
    height: 30px;
    padding: 0 5px 5px 5px;
}

.add {
    height: 20px;
    padding-left: 5px;
}